.account-navigation-link {
  @apply text-black no-underline font-bold uppercase text-sm p-6 pb-4 block border-b-4 border-grey-light;

  &:hover {
    @apply border-grey-dark;
  }
}

.account-navigation-link-active {
  @apply border-brand !important;
}



